import { Component, OnInit } from '@angular/core';
import { PerfilService } from 'app/modules/perfil/api/perfil.service';
import { Perfil } from 'app/modules/perfil/models/perfil.model';
import { PermissaoService } from 'app/modules/permissao/api/permissao.service';
import { Permissao } from 'app/modules/permissao/permissao.mode';
import { IFilter } from 'app/shared/models/interface.model';
import { PerfilPermissaoService } from '../../api/perfil-permissao.service';
import { PerfilPermissao } from '../../model/perfil-permissao.model';
import toastr from 'toastr';

@Component({
  selector: 'app-perfil-permissao-list',
  templateUrl: './perfil-permissao-list.component.html',
  styleUrls: ['./perfil-permissao-list.component.scss'],
})
export class PerfilPermissaoListComponent implements OnInit {
  constructor(
    private perfilService: PerfilService,
    private permissaoService: PermissaoService,
    private perfilPermissaoService: PerfilPermissaoService
  ) {}

  perfils: Perfil[] = [];
  permissoes: Permissao[] = [];
  perfilPermissao: PerfilPermissao[] = [];

  filtro: IFilter = {
    filtro: '',
    pagina: 0,
    direcaoOrdenacao: 'ASC',
    tamanho: 100,
    ordenarPor: 'id',
  };

  ngOnInit(): void {
    this.carregaInformacoesPerfilPermissao();
  }

  carregaInformacoesPerfilPermissao(): void {
    this.perfilService.getAll(this.filtro).subscribe(
      (perfils) => {
        this.perfils = perfils.content;
      },
      (error) =>
        toastr.error(
          'NÃ£o foi possÃ­vel carregar os perfils',
          error.error.error,
          {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          }
        )
    );

    this.permissaoService.getAll(this.filtro).subscribe(
      (permissoes) => {
        this.permissoes = permissoes.content;
      },
      (error) =>
        toastr.error(
          'NÃ£o foi possÃ­vel carregar os permissÃµes',
          error.error.error,
          {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          }
        )
    );

    this.carregaDadosPerfilPermissao();
  }

  atualizaPerfilPermissao(perfil: Perfil, permissao: Permissao): void {
    let perfilPermissaoRequest: PerfilPermissao = {
      ...this.perfilPermissao.find(
        (pp) => pp.perfilId === perfil.id && pp.permissaoId === permissao.id
      ),
    };
    perfilPermissaoRequest.status = !perfilPermissaoRequest.status;
    this.perfilPermissaoService.update(perfilPermissaoRequest).subscribe(
      () => {
        const index = this.perfilPermissao.findIndex(
          (pp) => pp.perfilId === perfil.id && pp.permissaoId === permissao.id
        );
        this.perfilPermissao[index].status = perfilPermissaoRequest.status;
        toastr.success('Permissao atualizada com sucesso!', '', {
          timeOut: 10000,
          progressBar: true,
          closeButton: true,
        });
      },
      (error) => {
        toastr.error(
          'NÃ£o foi possÃ­vel atualizar perfil e permissao selecionado',
          error.error.error,
          {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          }
        );
      }
    );
  }

  mostrarPermissao(perfil: Perfil, permissao: Permissao): boolean {
    return !!this.perfilPermissao.find(
      (pp) => pp.perfilId === perfil.id && pp.permissaoId === permissao.id
    );
  }

  permissaoAtivada(perfil: Perfil, permissao: Permissao): boolean {
    if (this.perfilPermissao.length < 1) return false;
    const perfilPermissao = this.perfilPermissao.find(
      (pp) => pp.perfilId === perfil.id && pp.permissaoId === permissao.id
    );

    if (!perfilPermissao) return false;

    return perfilPermissao.status;
  }

  private carregaDadosPerfilPermissao(): void {
    this.perfilPermissaoService.getAll().subscribe(
      (perfilPermissoes) => {
        this.perfilPermissao = perfilPermissoes;
      },
      (error) =>
        toastr.error(
          'NÃ£o foi possÃ­vel carregar perfils e permissÃµes',
          error.error.error,
          {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          }
        )
    );
  }
}
