import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment as env } from 'environments/environment';
import { Observable } from 'rxjs';
import { PerfilPermissao } from '../model/perfil-permissao.model';

@Injectable({
  providedIn: 'root',
})
export class PerfilPermissaoService {
  readonly apiPath = '/perfilpermissao';

  constructor(private http: HttpClient) {}

  getAll(): Observable<PerfilPermissao[]> {
    return this.http.get<PerfilPermissao[]>(
      `${env.apiAutenticacao}${this.apiPath}`
    );
  }

  getById(perfilPermissao: PerfilPermissao): Observable<PerfilPermissao> {
    return this.http.get<PerfilPermissao>(
      `${env.apiAutenticacao}${this.apiPath}/${perfilPermissao.perfilId}/${perfilPermissao.permissaoId}`
    );
  }

  insert(perfilPermissao: PerfilPermissao): Observable<null> {
    return this.http.post<null>(
      `${env.apiAutenticacao}${this.apiPath}`,
      perfilPermissao
    );
  }

  update(perfilPermissao: PerfilPermissao): Observable<null> {
    return this.http.put<null>(
      `${env.apiAutenticacao}${this.apiPath}/${perfilPermissao.perfilId}/${perfilPermissao.permissaoId}`,
      perfilPermissao
    );
  }

  delete(perfilPermissao: PerfilPermissao): Observable<null> {
    return this.http.delete<null>(
      `${env.apiAutenticacao}${this.apiPath}/${perfilPermissao.perfilId}/${perfilPermissao.permissaoId}`
    );
  }
}
