import { Component, OnInit } from '@angular/core';
import { PerfilService } from 'app/modules/perfil/api/perfil.service';
import { Perfil } from 'app/modules/perfil/models/perfil.model';
import { PermissaoService } from 'app/modules/permissao/api/permissao.service';
import { Permissao } from 'app/modules/permissao/permissao.mode';
import { IFilter } from 'app/shared/models/interface.model';
import { PerfilPermissaoService } from '../../api/perfil-permissao.service';
import { PerfilPermissao } from '../../model/perfil-permissao.model';
import toastr from 'toastr';

@Component({
  selector: 'app-perfil-permissao-vinculacao',
  templateUrl: './perfil-permissao-vinculacao.component.html',
  styleUrls: ['./perfil-permissao-vinculacao.component.scss'],
})
export class PerfilPermissaoVinculacaoComponent implements OnInit {
  perfils: Perfil[] = [];
  permissoes: Permissao[] = [];
  perfilPermissao: PerfilPermissao[] = [];

  filtro: IFilter = {
    filtro: '',
    pagina: 0,
    direcaoOrdenacao: 'ASC',
    tamanho: 100,
    ordenarPor: 'id',
  };

  constructor(
    private perfilService: PerfilService,
    private permissaoService: PermissaoService,
    private perfilPermissaoService: PerfilPermissaoService
  ) {}

  ngOnInit(): void {
    this.carregaInformacoesPerfilPermissao();
  }

  carregaInformacoesPerfilPermissao(): void {
    this.perfilService.getAll(this.filtro).subscribe(
      (perfils) => {
        this.perfils = perfils.content;
      },
      (error) =>
        toastr.error(
          'NÃ£o foi possÃ­vel carregar os perfils',
          error.error.error,
          {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          }
        )
    );

    this.permissaoService.getAll(this.filtro).subscribe(
      (permissoes) => {
        this.permissoes = permissoes.content;
      },
      (error) =>
        toastr.error(
          'NÃ£o foi possÃ­vel carregar os permissÃµes',
          error.error.error,
          {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          }
        )
    );

    this.perfilPermissaoService.getAll().subscribe(
      (perfilPermissoes) => {
        this.perfilPermissao = perfilPermissoes;
      },
      (error) =>
        toastr.error(
          'NÃ£o foi possÃ­vel carregar perfils e permissÃµes',
          error.error.error,
          {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          }
        )
    );
  }

  temPermissao(perfil: Perfil, permissao: Permissao): boolean {
    return !!this.perfilPermissao.find(
      (pp) => pp.perfilId === perfil.id && pp.permissaoId === permissao.id
    );
  }

  adicionarPermissao(perfil: Perfil, permissao: Permissao): void {
    this.perfilPermissaoService
      .insert({
        perfilId: perfil.id,
        permissaoId: permissao.id,
        status: true,
      })
      .subscribe(
        () => {
          toastr.success(
            `PermissÃ£o ${permissao.descricao} vÃ­nculada ao perfil ${perfil.nome} com sucesso!`,
            '',
            {
              timeOut: 10000,
              progressBar: true,
              closeButton: true,
            }
          );
          this.perfilPermissao.push({
            perfilId: perfil.id,
            permissaoId: permissao.id,
            status: true,
          });
        },
        (error) =>
          toastr.error('Erro ao vÃ­ncular nova permissÃ£o', error.error.error, {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          })
      );
  }

  removerPermissao(perfil: Perfil, permissao: Permissao): void {
    this.perfilPermissaoService
      .delete({
        perfilId: perfil.id,
        permissaoId: permissao.id,
        status: true,
      })
      .subscribe(
        () => {
          toastr.success(
            `PermissÃ£o ${permissao.descricao} removida do perfil ${perfil.nome} com sucesso!`,
            '',
            {
              timeOut: 10000,
              progressBar: true,
              closeButton: true,
            }
          );
          const index = this.perfilPermissao.findIndex(
            (pp) => pp.perfilId === perfil.id && pp.permissaoId === permissao.id
          );
          this.perfilPermissao.splice(index, 1);
        },
        (error) =>
          toastr.error('NÃ£o foi possÃ­vel remover vÃ­nculo', error.error.error, {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          })
      );
  }
}
